/* eslint-disable no-console */
import componentsSchemas from './assets/components-list'

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('SensorTime', component.exports)
}
export default {
  name: 'SensorTime',

  props: {
    sensor: {
      type: String,
      required: true
    },
    width: {
      type: Number,
      default: 150
    },
    height: {
      type: Number,
      default: 140
    }
  },

  data() {
    return {
      updatedSensor: null,
      updatedWidth: null,
      updatedHeight: null,
      showClock: true,
      agendaIcon: '/icons/aloes/agenda.svg',
      hands: [],
      colors: componentsSchemas.time.colors,
      aSide: true,
      months: [
        'Jan',
        'Feb',
        'Mar',
        'Apr',
        'May',
        'Jun',
        'Jul',
        'Aug',
        'Sep',
        'Oct',
        'Nov',
        'Dec'
      ],
      days: [
        { x: 7, y: 5 },
        { x: 6, y: 5 },
        { x: 5, y: 5 },
        { x: 4, y: 5 },
        { x: 3, y: 5 },
        { x: 2, y: 5 },
        { x: 1, y: 5 },
        { x: 7, y: 4 },
        { x: 6, y: 4 },
        { x: 5, y: 4 },
        { x: 4, y: 4 },
        { x: 3, y: 4 },
        { x: 2, y: 4 },
        { x: 1, y: 4 },
        { x: 7, y: 3 },
        { x: 6, y: 3 },
        { x: 5, y: 3 },
        { x: 4, y: 3 },
        { x: 3, y: 3 },
        { x: 2, y: 3 },
        { x: 1, y: 3 },
        { x: 7, y: 2 },
        { x: 6, y: 2 },
        { x: 5, y: 2 },
        { x: 4, y: 2 },
        { x: 3, y: 2 },
        { x: 2, y: 2 },
        { x: 1, y: 2 },
        { x: 7, y: 1 },
        { x: 6, y: 1 },
        { x: 5, y: 1 },
        { x: 4, y: 1 },
        { x: 3, y: 1 },
        { x: 2, y: 1 },
        { x: 1, y: 1 },
        { x: 7, y: 0 },
        { x: 6, y: 0 }
      ],
      weekDays: [
        { value: 'Su', x: 7, y: 0 },
        { value: 'Mo', x: 6, y: 0 },
        { value: 'Tu', x: 5, y: 0 },
        { value: 'We', x: 4, y: 0 },
        { value: 'Th', x: 3, y: 0 },
        { value: 'Fr', x: 2, y: 0 },
        { value: 'Sa', x: 1, y: 0 }
      ]
    }
  },

  computed: {
    viewBox() {
      return `0 0 ${this.updatedWidth} ${this.updatedHeight}`
    },
    radius() {
      return this.updatedWidth / 2.5
    },
    dateConverter() {
      //  const d = new Date(this.updatedSensor.resources["5506"] * 1000) || new Date();
      const d = new Date()
      return `${d.getDate()} ${this.months[d.getMonth()]} ${d.getFullYear()}`
    },
    getSeconds() {
      const d = new Date()
      return d.getSeconds()
    },
    calendarWidth() {
      return this.updatedWidth
    },
    calendarHeight() {
      return this.updatedHeight / 1.6
    }
  },

  watch: {
    sensor: {
      handler(sensor) {
        this.updatedSensor = JSON.parse(sensor)
      },
      immediate: true
    },
    width: {
      handler(width) {
        this.updatedWidth = width
      },
      immediate: true
    },
    height: {
      handler(height) {
        this.updatedHeight = height
      },
      immediate: true
    }
  },

  mounted() {
    console.log(this.updatedSensor)
    if (this.updatedSensor.type === 3333) {
      const nowTime = new Date()
      this.displayTime(nowTime)
    }
  },

  beforeDestroy() {},

  methods: {
    updateSensor(...args) {
      this.$emit('update-sensor', ...args)
    },

    deleteSensor(...args) {
      this.$emit('delete-sensor', ...args)
    },

    flipSide(value) {
      this.$emit('flip-side', value)
    },

    showDay(...args) {
      console.log('showDay', args)
    },

    calendarDayWidth(x) {
      return this.calendarWidth - (this.calendarWidth / 7) * x
    },
    calendarDayHeight(x) {
      return this.calendarHeight - (this.calendarHeight / 6) * x
    },

    displayTime(nowTime) {
      //  console.log("displayTime", date);
      document
        .querySelector(`#secondHandLine-${this.updatedSensor.id}`)
        .setAttribute(
          'transform',
          'rotate(' + (nowTime.getSeconds() / 60) * 360 + ')'
        )
      document
        .querySelector(`#minuteHandLine-${this.updatedSensor.id}`)
        .setAttribute(
          'transform',
          'rotate(' + (nowTime.getMinutes() / 60) * 360 + ')'
        )
      document
        .querySelector(`#hourHandLine-${this.updatedSensor.id}`)
        .setAttribute(
          'transform',
          'rotate(' + ((nowTime.getHours() % 12) / 12) * 360 + ')'
        )

      document.querySelector(
        `#calendarYear-${this.updatedSensor.id}`
      ).textContent = this.dateConverter
      const firstDay = new Date(
        nowTime.getFullYear(),
        nowTime.getMonth(),
        1
      ).getDay()
      const endDate = new Date(
        nowTime.getFullYear(),
        nowTime.getMonth() + 1,
        0
      ).getDate()
      let dayCounter = 1
      for (let i = 0; i <= 37; i++) {
        if (i > firstDay) {
          document.querySelector(
            `#calendarDay-${i}-${this.updatedSensor.id}`
          ).textContent = dayCounter < 10 ? '0' + dayCounter : dayCounter
          dayCounter++
          if (dayCounter > endDate) {
            break
          }
        }
      }
      document
        .querySelector(
          `#calendarDay-${firstDay + nowTime.getDate()}-${
            this.updatedSensor.id
          }`
        )
        .setAttribute('fill', '#E76C6C')
      document
        .querySelector(
          `#calendarDay-${firstDay + nowTime.getDate()}-${
            this.updatedSensor.id
          }`
        )
        .setAttribute('font-weight', 'bold')
    }
  }
}
