/* eslint-disable no-console */
import componentsSchemas from './assets/components-list'

if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('SensorSwitch', component.exports)
}
export default {
  name: 'SensorSwitch',

  props: {
    sensor: {
      type: String,
      required: true
    },
    width: {
      type: Number,
      default: 150
    },
    height: {
      type: Number,
      default: 140
    }
  },

  data() {
    return {
      updatedSensor: null,
      updatedWidth: null,
      updatedHeight: null,
      aSide: true
    }
  },

  computed: {
    viewBox() {
      return `0 0 ${this.updatedWidth} ${this.updatedHeight}`
    },
    colors() {
      return componentsSchemas.switch.colors
    }
  },

  watch: {
    sensor: {
      handler(sensor) {
        this.updatedSensor = JSON.parse(sensor)
      },
      immediate: true
    },
    width: {
      handler(width) {
        this.updatedWidth = width
      },
      immediate: true
    },
    height: {
      handler(height) {
        this.updatedHeight = height
      },
      immediate: true
    }
  },

  mounted() {},

  beforeDestroy() {},

  methods: {
    hasRightType(type) {
      return componentsSchemas.switch.list.find(objectId => objectId === type)
    },

    flipSide(value) {
      this.$emit('flip-side', value)
    },

    updateSensor(...args) {
      this.$emit('update-sensor', ...args)
    },

    deleteSensor(...args) {
      this.$emit('delete-sensor', ...args)
    }
  }
}
